





import Vue from 'vue';

/**
 * Roept oauth2Callback aan op de opener, hiermee wordt
 * de originele tab genformeerd over de autorisatie.
 */
export default Vue.extend({
  name: 'AuthCallback',
  created(): void {
    if (window.opener) window.opener.oauth2Callback(window.location.href);
    window.close();
  },
});
